<template>
  <section class="merchants-grid-header">
    <div class="merchants-grid-header__filters">
      <SearchInput
        v-model="filters.search"
        hide-shadow
        :placeholder="t('labels.searchDatabase')"
        class="merchants-grid-header__filter merchants-grid-header__filter-search"
      />
      <DateRange
        v-model="filters.dateRange"
        :placeholder-from="t('common.from')"
        :placeholder-to="t('common.to')"
        class="merchants-grid-header__filter merchants-grid-header__filter-date-range"
      />
      <SelectOption
        v-model="filters.module"
        placeholder="Select Module"
        selected-placeholder="Change Module"
      />
      <SelectOption
        v-model="filters.plan"
        placeholder="Select Plan"
        selected-placeholder="Change Plan"
      />
      <SelectOption
        v-model="filters.cycle"
        placeholder="Select Cycle"
        selected-placeholder="Change Cycle"
      />
    </div>
    <div class="merchants-grid-header__settings">
      <RadioButtons
        v-model="filters.status"
        :data="radioButtons"
      >
        <template #default="{ item }">
          {{ tp('table.filters.' + item.key) }}
        </template>
      </RadioButtons>
      <div class="d-flex align-items-center">
        <SelectSort v-model="filters.sort"/>
        <SelectShowOnPage v-model="perPage"/>
        <SelectColumns :columns="columns" />
      </div>
    </div>
  </section>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { computed, reactive } from 'vue'
import { useGetters, useMutations } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'
import DateRange from '@/shared/components/UIKit/Date/DateRange'
import SelectSort from '@/admin/components/Table/Filters/SelectSort'
import SearchInput from '@/admin/components/Inputs/SearchInput'
import RadioButtons from '@/shared/components/UIKit/RadioButtons'
import SelectColumns from '@/admin/components/Table/Filters/SelectColumns'
import SelectShowOnPage from '@/admin/components/Table/Filters/SelectShowOnPage'
import SelectOption from '@/shared/components/UIKit/Select/SelectOption'

export default {
  name: 'MerchantsGridHeader',
  components: {
    SelectOption,
    DateRange,
    SelectSort,
    SearchInput,
    RadioButtons,
    SelectColumns,
    SelectShowOnPage
  },
  setup () {
    const { t, tp } = useI18n('adminMerchants')
    const {
      getTableColumns: columns,
      getTableFilters: filters,
      getTablePerPage
    } = useGetters(MODULES_NAMES.MERCHANTS)

    const {
      SET_TABLE_PER_PAGE
    } = useMutations(MODULES_NAMES.MERCHANTS)

    const radioButtons = reactive([
      { key: 'all' },
      { key: 'active' },
      { key: 'inactive' },
      { key: 'suspended' }
    ])

    const perPage = computed({
      get: () => getTablePerPage.value,
      set: SET_TABLE_PER_PAGE
    })

    return {
      t,
      tp,
      filters,
      columns,
      perPage,
      radioButtons
    }
  }
}
</script>

