<template>
  <Dropdown class="select-option">
    <template #label>
      <SelectTitle
        :active="!!value"
        class="select-option__header"
      >
        <template v-if="!value">
          <Text
            mr="13px"
          >{{ placeholder }}</Text>
          <Icon name="arrow-down" />
        </template>
        <template v-else>
          <slot v-if="$slots.selected" name="selected" :option="value" />
          <Text v-else>{{ selectedPlaceholder }}</Text>
        </template>
      </SelectTitle>
    </template>

    <template #body>
      <Container
        bordered
        box-shadow
        background-color="white"
        class="select-show-on-page__body"
      >
        <div
          v-for="item in options"
          :key="item.id"
          class="select-show-on-page__item"
          @click="onSelect(item)"
        >
          <slot v-if="$slots.option" name="option" :item="item"/>
          <span v-else>{{ item }}</span>
        </div>
      </Container>
    </template>
  </Dropdown>
</template>

<script>
import { useModel } from '@/shared/utils/useModel'
import Text from '@/shared/components/UIKit/Text/Text'
import Icon from '@/shared/components/Icon/Icon'
import Dropdown from '@/shared/components/UIKit/Dropdown'
import SelectTitle from '@/shared/components/Containers/SelectTitle'
import Container from '@/shared/components/Containers/Container'

export default {
  name: 'SelectOption',
  components: {
    Container,
    Icon,
    Text,
    Dropdown,
    SelectTitle
  },
  props: {
    modelValue: {
      required: true
    },
    placeholder: {
      type: String,
      default: ''
    },
    selectedPlaceholder: {
      type: String,
      default: ''
    }
  },
  setup (props, { emit }) {
    const { value } = useModel(props, emit)

    const onSelect = (item) => {
      value.value = item
    }
    return {
      value,
      onSelect
    }
  }
}
</script>

<style lang="scss">
.select-option {
  &__header {
    background-color: map-get($colors, "default");
    border-color: map-get($colors, "default");
  }
}
</style>
